:host {
  position: absolute;
  transition: top .5s, left .5s, z-index .5s;
  overflow: hidden; }

:host([active]) {
  z-index: 600;
  left: 0 !important;
  top: 0 !important; }

#container {
  width: 0;
  height: 0;
  transition: width .5s, height .5s; }

:host([active]), :host([active]) #container {
  width: 100%;
  height: 100%; }

#header content[select=h1]::content h1 {
  text-align: center;
  margin: 0;
  padding: 0; }

:host([active]) #header content[select=h1]::content h1 {
  padding-right: 40px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

#opened, :host([active]) .cardIcon {
  display: none; }

#contentText {
  overflow-y: hidden; }

#contentText content::content ul {
  list-style: none;
  padding: 0 10%; }

:host([active]) #opened {
  display: block; }

#btnClose {
  position: absolute;
  top: 0;
  right: 0; }

